// Pagination
.pagination {
  display: flex;
  flex-direction: column;
  align-items: center;
  
  @include mobile {
    width: 288px;
    margin-top: 40px;
    margin-bottom: 60px;  
  }
  @include tablet {
    width: 415px;
    margin-top: 48px;  
    margin-bottom: 100px;  
  }
  @include desktop {
    width: 415px;
    margin-top: 60px;  
    margin-bottom: 150px;  
  }
}

.pagination__list {
  display: flex; 
  gap: 10px; //+  по макету 8px
 }

.pagination__item {
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--background-color-light); 
  border: 1px solid var(--color-active-btn); 
  border-radius: 2px; 
  cursor: pointer; 

  // color: var(--text-color);
  // fill: var(--text-color);
  // stroke: var(--text-color);
  
  @include mobile {
  width: 28px; 
  height: 28px; 
  @include font(500, 14, 19px, 0);
  
  }
  @include tablet {
     width: 33px; 
     height: 33px;
     @include font(500, 16, 22px, 0); 
  }
  @include desktop {
       width: 33px; 
       height: 33px; 
       @include font(500, 16, 2px, 0);
  }    
}

.pagination__item--current-page {
  background-color: var(--color-active-btn);
  color: var(--color-btn-white);
}

