.theme__track {
  height: 20px;
  width: 40px;
  display: inline-block;
  position: relative;
}

.theme__marker {
  background-color: var(--color-btn-white);
  border: .6 solid var(--markercolor);
  cursor: pointer;
  border-radius: 20px;
  align-items: center;
  display: flex;
  position: absolute;
  inset: 0;
}

.theme__marker:before {
  content: " ";
  cursor: pointer;
  background-color: var(--markercolor);
  height: 16px;
  width: 16px;
  border-radius: 50%;
  position: absolute;
  left: 1px;
}

.theme-switch__toggle ~ .theme__marker, .theme-switch__toggle ~ .theme__marker {
  border: 1px solid var(--markercolor);
}

.theme-switch__toggle:focus ~ .theme__marker, .theme-switch__toggle:hover ~ .theme__marker {
  box-shadow: 1px 2px 9px var(--markercolor);
}

.theme-switch__toggle:checked ~ .theme__marker, .theme-switch__toggle:checked ~ .theme__marker {
  box-shadow: 1px 2px 9px var(--text-color-dark-theme);
}

.theme-switch__toggle:checked ~ .theme__marker {
  background-color: var(--color-active-btn);
  border: 1px solid var(--color-btn-white);
  border-radius: 20px;
}

.theme-switch__toggle:checked ~ .theme__marker:before {
  background-color: var(--color-btn-white);
  transform: translateX(20px);
}

