.news-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row-reverse;
  justify-content: center;
  gap: 28px;

  @include tablet {
    gap: 30px;
  }

  @include desktop {
    gap: 32px;
  }
}

.news-item, .js-card {
  position: relative;
  border-radius: 4px;
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
  width: 288px;
  height: 630px;

  @include tablet {
    width: 353px;
    height: 675px;
  }

  @include desktop {
    width: 394px;
    height: 675px;
  }
}
  
